.footer_bg{
    background-color: #474545;
    color: #fff;
    max-width: 100%;
    padding: 25px 0;
}

.footer_main{
    max-width: 1100px;
    margin: auto;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
}

.footer_main p{
    font-weight: 300;
    padding-top: 1.7px;
}

.col_logo{

}


.col_about p{
    width: 250px;
    
}


 @media (max-width:600px) {
    .col_about p{
        width: 200px;
        
    }   
    
    .footer_main p{
        font-size: 14px;
    }
 }

 @media (max-width:600px) {
    .footer_main p{
        font-size: 18px;
    }

    .footer_main{
        flex-direction: column;
    }

    .col_logo, .col_contact, .col_about{
        margin: auto;
        margin-bottom: 20px;
    }
 }