    /* background-color: rgba(134, 130, 130, 0.288); */
    /* width: 100% ; */
    /* height: calc(100vh - 70px); */
}
.card_info_img{
    /* position: absolute; */
    width: 100%;
    /* z-index: -10; */
}

.card_info_img img{
    width: 100% ;
    max-height: calc(100vh - 70px);
    object-fit: cover;
}


.card_info_details_bg{
    background-color: #dadada;
    
}

.card_info_details_container{
    max-width: 750px;
    margin: auto;
    padding: 35px 0;
}

.card_info_section1_flex{
    display: flex;
    justify-content: space-between;
    padding: 25px 0;
}

.card_info_btn_map p{
    margin-top: 20px;
    padding: 10px 25px;
    background-color: #157FDA;
    border-radius: 50px;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
    transition: 0.3s;
}

.card_info_btn_map p:hover{
   transform: scale(1.05);
}

.horizontal_hr{
    border-top: 2px solid rgba(0, 0, 0, 0.514);
}

.card_info_treatment{
    padding: 20px 0;
    max-width: 600px;
    margin: auto;
}



.card_info_section2_flex{
    display: flex;
    justify-content: space-between;
    padding-top: 2.5px;
    max-width: 580px;
    margin: auto;
}

.card_info_doctor h3{
    padding-bottom: 2.5px;
}

.card_info_doctor p{
    padding-left: 10px;
}


.vertical_hr{
    border-left: 2px solid rgba(0, 0, 0, 0.514);
    margin: 10px 0;
}

.card_info_about{
    padding: 25px 0;
}

.card_info_doctor{
    width: 500px;
    margin: auto;
    padding: 20px 0;
}

.card_info_about_container{
    max-width: 650px;
    margin: auto;
    padding: 10px 0;
}

.card_info_end_hr{
    width: 20%;
    margin: auto;
}