.about {
  max-width: 100%;
  /* height: ; */
  background-color: rgb(194, 215, 255);
}
.about h2 {
  color: black;
  font-size: 32px;
  /* padding-left: 12px; */
  text-align: center;
  padding-top: 20px;
  
}
.About_rapper {
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 950px;
  /* background: brown; */
}
.About_selection1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  margin: 40px;
}
.About_selction1_text p,.About_selction2_text p{
  padding-top: 2px;
  /* padding-left: 4px; */
}
.About_selction1_text h3,.About_selction2_text h3{
  padding-top: 15px;
}

.About_selction1_text h3, .About_selction2_text h3{
  font-size: 27px;
  font-weight: 700;
}
.About_selction1_text p, .About_selction2_text p{
  font-size: 20px;
  font-weight: 400;
}
.About_selection1_image img {
  width: 350px;
}
.About_selction1_text {
  width: 350px;
}
.About_selection2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  margin: 40px;
}
.About_selection2_image img {
  width: 350px;
}
.About_selction2_text {
  width: 350px;
}
.About_team {
  text-align: center;
}
.About_row {
  display: flex;
  flex-wrap: wrap;
  /* padding: 20px; */
  justify-content: space-around;
}
@media (max-width: 850px) {
  .About_selction2_text {
    width: 300px;
  }
}

@media (max-width: 780px) {
  .About_selction2_text {
    width: 250px;
  }
}
@media (max-width: 740px) {
  .About_selction2_text {
    width: 210px;
  }
}

@media (max-width: 740px) {
  .About_selction2_text {
    width: 180px;
  }
}

@media (max-width: 730px) {
  .About_selection1 {
    display: flex;
    flex-direction: column-reverse;
    /* justify-content: space-between; */
    max-width: 100%;
    margin: auto;
  }
  .about h2 {
    text-align: center;
  }
  .About_selection2 {
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    max-width: 100%;
    margin: auto;
  }
  .About_selction2_text,
  .About_selction1_text {
    width: 350px;
    text-align: center;
  }
}
@media (max-width: 360px) {
  .About_selction2_text,
  .About_selction1_text {
    width: 180px;
    text-align: center;
    margin: auto;
  }
  .About_selection2 {
    text-align: center;
  }
  .About_selection2_image img,
  .About_selection1_image img {
    width: 250px;
  }
  .about h2 {
    color: black;
    font-size: 35px;
    /* padding-left: 12px; */
    margin: 0;
    padding: 20px;
  }
}
