.contact {
    /* background-color: rgb(255, 255, 255); */
    max-width: 100%;
  }
  .contact_polygon {
    width: 100%;
    height: 100% ;
    position: absolute;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
    background-color: rgb(174, 223, 238);
    z-index: -5;
  }
  .contact_rapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 800px;
    margin: auto;
    padding-top: 100px;
    padding-bottom: 140px;
  }
  .contact_text {
    width: 300px;
    text-align: center;
    padding-top: 130px;
    /* margin: auto; */
  }
  .feedback_form {
    width: 350px;
    border: 1px solid blue;
    /* margin: auto; */
    padding-top: 20px;
    background-color: white;
  }
  .feedback_form h3,
  .feedback_form p {
    font-size: 20px;
    text-align: center;
  }
  .feedback_form_rapper {
    display: flex;
    flex-direction: column;
  }
  .ContactName input,
  .ContactEmail input,
  .ContactFeedback textarea {
    padding: 10px;
    width: calc(100% - 55px);
    margin: 10px 0 10px 15px;
    font-size: 15px;
    outline: none;
  }
  .ContactFeedback textarea {
    /* height: 100px; */
    resize: none;
  }
  .Contact_button {
    padding: 10px 40px;
    cursor: pointer;
    border-radius: 50px;
    background-color: rgb(103, 103, 255);
    color: white;
    border: none;
    outline: none;
   
    /* margin: auto; */
  }
  .Contact_button_rapper{
      display: flex;
    justify-content: center;
    padding-bottom: 15px;
    
  }
  
  .Contact_button:hover {
    background-color: rgb(45, 45, 248);;
    
  }
  @media (max-width: 870px) {
    .contact_rapper {
      flex-direction: column;
      padding-top: 20px;
    }
    .contact_text {
      padding-top: 0px;
        padding-bottom: 20px;
      margin: auto;
    }
    .feedback_form {
      margin: auto;
    }
   
  }
  @media (max-width: 420px) {
    .feedback_form {
      border: none;
      background-color: rgba(252, 252, 252, 0);
    }
  }
  @media (max-width: 330px) {
    .feedback_form {
      width: 300px;
      border: none;
      background-color: rgba(252, 252, 252, 0);
    }
  }
  