.hos_card{
    flex: 0 0 50%;
    margin-bottom: 20px;
    display: flex;
}
.hos_card_bg{
    margin: auto;
    background-color: #fff;
    padding: 15px 20px;
    width: 420px;
    /* height: 220px; */
    transition: 0.5s;
}

.hos_card_bg:hover{
    transform: scale(1.07);
}

.hos_card_treatment_rapper{
    margin-top: 7px;
}
.hos_card_treatment_headline{
    font-weight: 700;
}
.hos_card_treatment_flex{
    display: flex;
    justify-content: space-between;
    padding-top: 4px;
    width: 400px;
    margin: auto;
}

.hos_card_treatment_flex hr{
    /* height: 80%; */
    border-left: 1px solid #000;
}

.hos_card_treatment_flex p{
    font-weight: 300;
    padding-top: 1.7px;
}

.hos_card_treatment_section1, .hos_card_treatment_section2{
    /* width: 180px; */
    flex:1
}

@media (max-width:500px) {
    .hos_card_bg{
        width: 280px;
        height: 180px;
        /* padding: 10px; */
    }
    .hos_card_treatment_flex{
        width: 280px;
    }
}



