textarea{
    resize: vertical;
    font-size: 15px;
}

.data_entry_bg{
    background-color: #E1E1E1;
    max-width: 100%;
}

.data_entry_container{
    max-width: 700px;
    margin: auto;
    padding: 35px 0;
}

.data_entry_basic_info{
    max-width: 750px;
    background-color: #fff;
    padding: 25px;
}

.data_entry_basic_info_flex{
    display: flex;
    justify-content: space-between;
}

.data_entry_basic_info_section1,.data_entry_basic_info_section2{
    width: 300px;
}

.data_entry_basic_info_section2 select, .data_entry_basic_info_section1 select{
    width: 100%;
    padding: 7px;
    margin-top: 10px;
}

.data_entry_basic_info_section1 input,.data_entry_basic_info_section2 input{
    width: calc(100% - 18px);
    padding: 7px;
    margin-top: 10px;
}

.data_entry_basic_info textarea{
    padding: 10px;
    margin-top: 10px;
    width: calc(100% - 20px);
}



.data_entry_contact, .data_entry_treatment, .data_entry_doctor, .data_entry_about{
    max-width: 750px;
    background-color: #fff;
    padding: 25px;
    margin-top: 25px;
}



.data_entry_contact_flex{
    display: flex;
    justify-content: space-between;
}

.data_entry_contact_section1, .data_entry_contact_section2{
    width: 300px;
}

.data_entry_contact_section1 input, .data_entry_contact_section2 input{
    width: calc(100% - 14px);
    padding: 7px;
    margin-top: 10px;
}



.data_entry_treatment_flex{
    display: flex;
    justify-content: space-between;
}

.data_entry_treatment_sector1,.data_entry_treatment_sector2{
    width: 300px;
}

.data_entry_treatment_sector1 input,.data_entry_treatment_sector2 input{
    width: calc(100% - 14px);
    padding: 7px;
    margin-top: 10px;
}

.data_entry_doctor_flex{
    display: flex;
    justify-content: space-between;
}

.data_entry_doctor_sector1,.data_entry_doctor_sector2{
    width: 300px;
}

.data_entry_doctor_sector1 input,.data_entry_doctor_sector2 input{
    width: calc(100% - 14px);
    padding: 7px;
    margin-top: 10px;
}

.data_entry_img{
    max-width: 750px;
    background-color: #fff;
    padding: 25px;
    margin-top: 20px;
}

.data_entry_about textarea{
    margin-top: 10px;
    width: calc(100% - 14px);
    padding: 7px;
}

.data_entry_submit_btn{
    padding-top: 25px;
    text-align: center;
}

.data_entry_submit_btn input{
    padding: 12px 30px;
    background-color: #157FDA;
    color: #fff;
    border-radius: 50px;
    outline: none;
    border: none;
    /* margin: auto; */
    cursor: pointer;
    transition: 0.5s;
}

.data_entry_submit_btn input:hover{
    transform: scale(1.2);
    background-color: #025ba8;
}