.feedback_view_bg{
    background-color: rgb(170, 197, 255);
    min-height: 100vh;
    
}

.feedback_view_rapper{
    display: flex;
    flex-wrap: wrap;
    
}

.feedback_view_card_rapper{
    flex:0 0 33.33%;
    padding-top: 20px;
}

.feedback_view_card{
    background-color: #fff;
    width: 320px;
    padding: 15px;
    
}

.feedback_view_card p{
    padding-top: 2.5px;
    font-weight: 300;
}

.feedback_view_card {
    margin: auto;
}