.carousel{
    
   
}


.carousel_img img{
    max-width: 100vw ;
    max-height: calc(100vh - 70px);
    object-fit: cover;
}

.on_carousel{
    /* position: absolute; */
    max-width: 100vw ;
    max-height: calc(100vh - 70px);
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.75);
}

.heading_text{
    position: absolute;
    z-index: 5;
    top:40%;
    left:20%;
    animation-duration: 2s;
    animation-name: textup;
}


@keyframes textup {
    from {
      opacity: 0;
      transform: translate3d(0, -200px, 0);
    }
    to {
      opacity: 1;
      transform: translate3d(0, 0px, 0);
    }
  }

.heading_text p{
    font-size: 40px;
    color: #fff;
  
}

.heading_text p span{
    font-size: 45px;
    font-weight: 700;
    color: rgb(216, 8, 8);
}

.btn_lern_more{
    display: inline-block;
    cursor: pointer;
}

.btn_lern_more p{
    background-color: #157FDA;
    padding: 15px 40px;
    font-size: 25px;
    border-radius: 50px;
}




.hos_filter_bg{
    max-width: 100vw;
    height: 80px;
    background-color: #157FDA;
}

.hos_filter{
    display: flex;
    flex-wrap: wrap;
    margin: 0px 120px;
    justify-content: space-between;
}

.type, .sector, .location, .search, .hospital_name_search{
    margin-top: 20px;
}

.input_type, .input_sector, .input_name_search, .search_btn{
    outline: none;
    border: none;
    width: 200px;
    padding: 8px;
    /* font-size: 17px; */
}

.input_type, .input_sector{
    cursor: pointer;
}

.search_btn{
    cursor: pointer;
    transition: 0.3s;
}
.search_btn:hover{
    transform: scale(1.05);
}
