@media (max-width:1280px) {
    .heading_text{
        top: 30%;
    }
    .heading_text p{
        font-size: 30px;
    }
    .heading_text p span{
        font-size: 35px;
    }
    .btn_lern_more p{
        font-size: 20px;
        padding: 10px 30px;
    }
    .hos_filter{
        margin: 0 60px;
    }
    .hos_filter{
        margin: 0 30px;
    }
    .type, .sector, .location, .search, .hospital_name_search{
        margin-top: 23px;
    }
    
    .input_type, .input_sector, .search_btn, .input_name_search{
        width: 170px;
        padding: 8px;
        font-size: 12px;
    }
}

@media (max-width:950px) {
    .heading_text{
        top: 25%;
        left: 17%;
    }
    .heading_text p{
        font-size: 20px;
    }
    .heading_text p span{
        font-size: 25px;
    }
    .btn_lern_more p{
        font-size: 15px;
        padding: 5px 20px;
    }
    .hos_filter{
        margin: 0 30px;
    }
    .type, .sector, .location, .search, .hospital_name_search{
        margin-top: 25px;
    }
    
    .input_type, .input_sector,.input_name_search, .search_btn{
        width: 120px;
        padding: 8px;
        
        font-size: 12px;
    }
    
}

@media (max-width:760px) {
    .hos_filter{
        flex-direction: column;
        /* align-items: center; */
    }
    .input_type, .input_sector,  .search_btn{
        width: 100%;
        margin: auto;
        
    }
    .input_name_search{
        width: calc(100% - 16px);
    }
    .hos_filter_bg{
        height: 300px;
    }
    .hos_filter{
        margin: 0px 60px;
    }
}

@media (max-width:470px) {
    .heading_text{
        top: 17%
    }
}

