.Team_card_col {
  flex: 0 0 25%;
  padding: 20px 0;
}
.Team_card_rapper {
  width: 200px;
  margin: auto;

  /* margin: 10px; */
  background-color: rgb(255, 255, 255);
  transition: all 0.5s ease;
}
.Team_card_rapper:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 50px rgba(0, 0, 0, 0.2);
}

.Team_card_img img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  padding: 20px;
}
.Team_card_info {
  text-align: center;
  padding-bottom: 10px;
}
