.main_container_bg{
    
    background-color: #E1E1E1;
    width: 100%;
    padding: 60px 0;
}

.main_container{
    max-width: 1100px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}


.pagination{
    margin-top: 15px;
    display: flex;
    list-style: none;
    cursor: pointer;
    justify-content: center;
}

.pagination a{
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #203bd6;
  color: #203bd6;
  margin: 0 5px;
}

.active_page_link a{
    background-color: #203bd6;
    color: #fff;
}

.disable_page_link a{
    color: rgb(198, 197, 202);
    border: 1px solid rgb(198, 197, 202);
    cursor: no-drop;
}

@media (max-width:990px) {
    .main_container{
        flex-direction: column;
    }
}