.nav_bg{
    max-width: 100%;
    height: 70px;
    background-color: #157FDA;
}

.nav_container{
    margin: 0 120px;
    display: flex;
    justify-content: space-between;
}

@media (max-width:800px) {
    .nav_container{
        margin: 0 60px;
    }
}

@media (max-width:650px) {
    .nav_container{
        margin: 0 10px;
    }
}

.nav_img img{
    height: 70px;
}

.nav_links{
    display: flex;
}

.nav_links p{
    font-size: 20px;
    font-weight:400;
    padding: 23px 15px;
    cursor: pointer;
    color: #fff;
    transition: 0.6s;
}

.nav_links p:hover{
    background-color: #0c4d86;
}

.nav_btn{
    position: absolute;
    top: 0;
    right: 0;
    display: none;
}

.nav_btn p{
    padding: 26px 15px;
    color: #fff;
    transition: 0.6s;
    cursor: pointer;
}
.nav_btn p:hover{
    background-color: #0c4d86;
}

.nav_hide{
    display: none;
}

@media (max-width:500px) {
    .nav_links{
        display: none;
    }
    .nav_btn{
        display: block;
    }
    
}



#mobile_nav_links{
    position: absolute;
    top:70px;
    left:0;
    flex-direction: column;
    width: 100%;
    z-index: 10;
    display: block;
} 

#mobile_nav_links p{
    text-align: center;
    background-color: rgb(0, 0, 0);
}  
#mobile_nav_links p:hover{
    background-color: rgb(126, 30, 30);
}